
<template>
  <div class="commonQuery" >
    <el-container :style="{'height':siteContentViewHeight +'px'}">
      <el-main>
        <div class="search table-header-btn">
          <!-- 用户管理主表 -->
          <div class="table-header-btn-l">
          </div>
          <div class="table-header-btn-r">
            <el-button type="primary" size="small" @click="addQuery">添加</el-button>
            <el-button type="primary" size="small" @click="moveDirection('up')">上移</el-button>
            <el-button type="primary" size="small" @click="moveDirection('down')">下移</el-button>
            <el-button type="primary" size="small" @click="saveSort">保存排序</el-button>
          </div>
        </div>
        <el-table :height="siteContentViewHeight -100 +'px'" ref="multipleTable" :data="tableData" @selection-change="selectionChangeHandle" tooltip-effect="dark" style="width: 100%">
          <el-table-column type="selection" width="55" align="center"></el-table-column>
          <el-table-column
            width="50px"
            fixed
            type="index"
            :index="indexMethod"
            label="序号"
            align="center"
          ></el-table-column>
          <el-table-column prop="queryCode" label="名称"></el-table-column>
          <el-table-column prop="queryName" label="分类"></el-table-column>
          <el-table-column prop="queryStatus" label="启用状态状态">
            <template slot-scope="scope">
              <el-tag size="medium" type="danger" v-if="scope.row.queryStatus=='1'">禁用</el-tag>
              <el-tag size="medium" type="success" v-else-if="scope.row.queryStatus=='0'">启用</el-tag>
            </template>
          </el-table-column>
          <el-table-column fixed="right" label="操作" align="center" width="350px">
            <template slot-scope="scope">
              <el-button
                size="mini"
                plain
                type="primary"
                title="编辑"
                @click="updateQuery(scope.row, scope.$index)"
              >编辑</el-button>
              <el-button
                title="删除"
                size="mini"
                plain
                type="danger"
                @click="delQuery(scope.row, scope.$index)"
              >删除</el-button>
            </template>
          </el-table-column>
        </el-table>
        <!--分页-->
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="page.pageNum"
          :page-sizes="page.pageSizes"
          :page-size="page.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="page.total"
        ></el-pagination>
        <!-- 弹出框--用户添加 -->
        <el-dialog
          :title="dialogTitle"
          :visible.sync="queryUpdateVisable"
          v-if="queryUpdateVisable"
          :close-on-click-modal="false"
          width="40%"
        >
          <query-update :currentRow="currentRow" :isEdit="isEdit" @updateCallback="updateCallback"></query-update>
        </el-dialog>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import queryUpdate from "./queryUpdate.vue";
import {swapItems} from '@/utils';

export default {
  name: "commonQuery",
  components: {
    queryUpdate
  },
  props:['siteContentViewHeight'],
  data() {
    return {
      Height: {
        height: document.documentElement.clientHeight - 200 + "px"
      },
      queryUpdateVisable: false,
      dialogTitle: "",
      search: {
        org: "",
        code: "",
        name: ""
      },
      treeData: [], //机构数据
      nodeId: "", //选中机构id
      tableData: [], //列表数据
      currentRow: {}, //用户当前选中行
      isEdit: true, //true 编辑
      keyWord: "",
      page: {
        pageNum: 1,
        pageSize: 10,
        pageSizes: [10, 20, 30, 40, 50],
        total: 0
      },
      flag: false //组织机构树不要初始值
    };
  },
  created() {
    this.getQueryData();
  },
  methods: {
    indexMethod(index) {
      return index + 1;
    },
    //用户列表分页查询
    getQueryData() {
      let _this = this;
      _this
        .$http({
          url: _this.$http.adornUrl(_this.API.getSysPowerquery),
          method: "post",
          data: _this.$http.adornData({
            orgId: _this.search.org,
            queryCode: _this.search.code,
            queryName: _this.search.name
          })
        })
        .then(({ data }) => {
          if (data.code === 0) {
            _this.tableData = data.data.records;
          } else {
            _this.$message.error(res.msg);
          }
        })
        .catch(function(error) {
          _this.$message.error(error);
        });
    },

    //设置分页大小
    handleSizeChange(pageSize) {
      this.page.pageSize = pageSize;
      this.getQueryData();
    },
    //当前页改变
    handleCurrentChange(pageNum) {
      this.page.pageNum = pageNum;
      this.getQueryData();
    },
    //查询
    searchUser() {
      this.getQueryData();
    },
    addQuery() {
      this.currentRow.orgId = this.nodeId;
      this.dialogTitle = "添加查询条件";
      this.queryUpdateVisable = true;
      this.isEdit = false;
    },
    updateQuery(row) {
      this.currentRow = row;
      this.dialogTitle = "编辑查询条件";
      this.queryUpdateVisable = true;
      this.isEdit = true;
    },
    //用户操作回调
    updateCallback(flag) {
      if (flag) {
        this.getQueryData();
      }
      this.queryUpdateVisable = false;
    },
    //删除用户
    delQuery(row) {
      this.$confirm("确定删除本条数据？", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.$http({
          url: this.$http.adornUrl(this.API.deleteSysPowerquery),
          method: "post",
          data: this.$http.adornData({
            queryId: row.queryId
          })
        })
          .then(({ data }) => {
            if (data.code === 0) {
              this.$message.success("删除成功");
              this.getQueryData();
            } else {
              this.$message.error("删除失败");
            }
          })
          .catch(function(error) {});
      });
    },
    selectionChangeHandle(val) {
      //多选
      this.tableDataSelections = val;
    },
    moveDirection(direction) {
      //移动
      var that = this,
        index;
      that.tableDataSelections.sort((prev, next) => {
        return that.tableData.indexOf(prev) - that.tableData.indexOf(next);
      });
      if (direction == "down") that.tableDataSelections.reverse();
      that.tableDataSelections.forEach(item => {
        index = that.tableData.indexOf(item); //得到右侧选中值的标识坐标
        if (direction == "up") that.moveUp(index);
        else if (direction == "down") that.moveDown(index);
      });
    },
    moveUp(index) {
      //上移
      if (index == 0) return;
      this.tableData = swapItems(this.tableData, index, index - 1, "up");
    },
    moveDown(index) {
      //下移
      if (index == this.tableData.length - 1) return;
      this.tableData = swapItems(this.tableData, index, index + 1, "down");
    },
    saveSort() {
      //保存排序
      var sortArr = [];
      if (this.tableData.length < 0) return;
      this.tableData.forEach((item, index) => {
        sortArr.push({
          queryId: item.queryId,
          orderno: index + 1
        });
      });
      this.$http({
        url: this.$http.adornUrl(this.API.querySort),
        method: "post",
        data: this.$http.adornData({
          data: JSON.stringify(sortArr)
        })
      }).then(({ data }) => {
        if (data && data.code === 0) {
          this.getQueryData()
          this.$message.success("操作成功");
        } else {
          this.$message.error(data.msg);
        }
      });
    }
  },
  watch: {
    filterText(val) {
      this.$refs.orgTree.filter(val);
    },
    fullHeight(val) {
      if (!this.timer) {
        this.fullHeight = val;
        this.timer = true;
        let that = this;
        setTimeout(function() {
          that.timer = false;
        }, 400);
      }
    }
  },
  mounted() {
    const that = this;
    window.addEventListener('resize', () => {
      return (() => {
        window.fullHeight = document.documentElement.clientHeight;
        that.fullHeight = window.fullHeight;
      })()
    }, false)


  }
};
</script>

<style lang="scss" scoped>
.organizationTreeSelect {
  display: inline-block;
}
.el-pagination {
  /*position: fixed;*/
  /*right: 20px;*/
  /*bottom: 20px;*/
}
.search {
  justify-content: space-between;
  margin-top: 0px;
  .labelName {
    padding: 0 15px;
    line-height: 33px;
  }
  .el-input {
    width: 180px;
    margin-right: 10px;
  }
}
>>> .el-main{
  padding-bottom: 0;
}
</style>



